.none {
	display: none !important;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.no-scroll {
	overflow-y: hidden;
}

.overlay {
	position: fixed;
	inset: 0px;
	z-index: 101;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.75);
	opacity: 0;
	transition: opacity 0.3s ease-in-out;
	will-change: opacity;
	pointer-events: none;

	&.shown {
		opacity: 1;
	}
}

.no-hover {
	pointer-events: none;
}